<!-- eslint-disable vue/attribute-hyphenation -->
<!-- eslint-disable vue/singleline-html-element-content-newline -->
<!-- eslint-disable vue/attributes-order -->
<!-- eslint-disable vue/max-attributes-per-line -->
<!-- eslint-disable vue/html-self-closing -->
<template>
  <div>
    <modal-component ref="modalList" modalClass="!w-auto" modalPosition="center">
      <template #slotBody>
        <div class="w-full">
          <h3 class="text-xl mb-2">Aniqlangan bolalar r`oyxati</h3>
          <hr />
          <ul class="w-full mt-4">
            <li class="w-full flex items-center justify-between gap-10" v-for="(n, i) in juvenile_table_data" :key="i">
              <span>
                {{ n.surname + ' ' + n.name + ' ' + n.patronym }}
              </span>
              <button class="px-4 flex gap-4 py-2 text-white bg-primary rounded-md" @click="requestBirthPassportData(n)">
                Tanlash
                <Icon icon="mdi:account-plus" width="20" height="20" />
              </button>
            </li>
          </ul>
        </div>
      </template>
    </modal-component>
    <form @submit="onSubmit">
      <section class="section_wrapper">
        <div class="">
          <section v-if="selectedJuvenile.id" class="max-w-[600px] bg-white mb-[12px]">
            <PassportCard :id="selectedJuvenile.id" :passport-info="selectedJuvenile.personal_info"
              @canceled="juvenileDeleteHandler" @click="juvenileSelectHandler" />
          </section>
          <span class="text-primary text-[1.1em]]">
            {{ notFoundError }}
          </span>
        </div>
        <div class="grid grid-cols-2 gap-4 2xl:w-[70%]">
          <div class="form_wrapper my-3">
            <label class="input_label" for="passport_type"> Hujjat turi </label>
            <SelectBox id="passport_type" v-model="passport_type" :options="passportTypes" show-by="text" track-by="id"
              placeholder="Hujjat turini tanlang" />
            <span class="error_message">{{ passport_typeError }}</span>
          </div>
          <template v-if="passport_type">
            <div v-if="allowedToPutForeignCountry" class="form_wrapper my-3">
              <label class="input_label" for="passport_type"> Horijiy davlat </label>
              <SelectBox id="foreign_country" v-model="foreign_country" :options="countriesList" show-by="name"
                track-by="id" placeholder="Horijiy davlatni tanlang" />
              <span class="error_message">{{ foreign_countryError }}</span>
            </div>
            <div v-if="allowedToFillSeriyaNumber" class="flex gap-4 my-3">
              <div class="form_wrapper">
                <label class="input_label" for="passport_seria"> Hujjat seriasi </label>
                <input id="passport_seria" v-model="passport_seria" v-validate-cyrillic v-maska
                  :data-maska="allowBirthCertificate ? '' : '@@'" placeholder="" class="base_input uppercase w-[150px]"
                  name="passport_seria" type="text" />
                <span class="error_message">{{ passport_seriaError }}</span>
              </div>
              <div class="form_wrapper w-full">
                <label class="input_label" for="passport_number">Hujjat raqami</label>
                <div class="flex items-center gap-2 w-full">
                  <input v-validate-cyrillic id="passport_number" v-maska
                    :data-maska="allowBirthCertificate ? '' : '#######'" v-model="passport_number" placeholder=""
                    class="base_input uppercase" :class="!allowBirthCertificate && 'w-full'" name="passport_number"
                    type="text" />
                  <button v-if="allowBirthCertificate"
                    class="self-start px-[12px] py-[6px] bg-primary h-[45px] text-white hover:opacity-40 rounded"
                    @click.prevent="searchJuvenileByPINFL">
                    Qidirish
                  </button>
                </div>
                <span class="error_message">{{ passport_numberError }}</span>
              </div>
            </div>
            <div class="form_wrapper w-full" v-if="allowBirthCertificate">
              <label class="input_label" for="pinfl"> JSHSHIR </label>
              <div class="flex items-center gap-2 w-full">
                <input v-validate-cyrillic id="pinfl" :disabled="isIntegrationData" v-model="pinfl" v-maska
                  data-maska="##############" placeholder="JSHSHIR ni kiriting" class="base_input w-full" name="pinfl" />
              </div>
              <span class="error_message">{{ pinflError }}</span>
            </div>
            <div v-if="allowedToFillSeriyaNumberForPINFL" class="form_wrapper my-3">
              <label class="input_label" for="pinfl"> JSHSHIR </label>
              <div class="flex items-center gap-2 w-full">
                <input v-validate-cyrillic id="pinfl" v-model="pinfl" v-maska data-maska="##############"
                  placeholder="JSHSHIR ni kiriting" class="base_input w-full" name="pinfl" />
                <button
                  class="self-start w-[150px] px-[12px] py-[6px] bg-primary h-[45px] text-white hover:opacity-40 rounded"
                  @click.prevent="searchJuvenileByPINFL">Qidirish</button>
              </div>
              <span class="error_message">{{ pinflError }}</span>
            </div>
          </template>
        </div>
        <div class="2xl:w-[60%]">
          <div v-if="allowedToPutReferenceType" id="reference_type" class="form_wrapper my-3">
            <label class="input_label"> Ma'lumotnoma </label>
            <FileInput id="reference_type1" v-model:files="reference_type" :initial-files="initialReferenceFile"
              :multiple-files="false">
              <template #icon>
                <div class="flex justify-between shadow rounded w-full">
                  <p
                    class="flex items-center text-gray-400 px-3 overflow-hidden overflow-ellipsis base_input border-none">
                    Ma'lumotnomani yuklang</p>
                  <span
                    class="flex px-4 items-center justify-center rounded bg-primary text-white tracking-wide text-sm cursor-pointer">
                    YUKLASH </span>
                </div>
              </template>
            </FileInput>
            <span class="error_message">{{ reference_typeError }}</span>
          </div>
        </div>
        <div class="grid grid-cols-2 xl:grid-cols-3 gap-4 2xl:w-[60%]">
          <div class="form_wrapper my-3">
            <label class="input_label" for="last_name"> Familiya </label>
            <input :disabled="isIntegrationData" v-validate-cyrillic id="last_name" v-model="last_name" class="base_input"
              placeholder="Familiyasini kiriting" name="last_name" type="text" />
            <span class="error_message">{{ last_nameError }}</span>
          </div>
          <div class="form_wrapper my-3">
            <label for="first_name" class="input_label"> Ismi </label>
            <input :disabled="isIntegrationData" v-validate-cyrillic id="first_name" v-model="first_name"
              placeholder="Ismini kiriting" class="base_input" name="last_name" type="text" />
            <span class="error_message">{{ first_nameError }}</span>
          </div>
          <div class="form_wrapper my-3">
            <label class="input_label" for="father_name"> Sharifi </label>
            <input :disabled="isIntegrationData" v-validate-cyrillic id="father_name" v-model="father_name"
              placeholder="Sharifini kiriting" class="base_input" name="father_name" type="text" />
            <span class="error_message">{{ father_nameError }}</span>
          </div>
          <div class="form_wrapper my-3">
            <label class="input_label"> Tug‘ilgan sanasi </label>
            <DateComponent :disabled="isIntegrationData" v-model="birth_date" placeholder="Sanani tanlang" :options="{
              minDate: birthDateRange.from,
              maxDate: birthDateRange.to,
              startDate: birthDateRange.from,
            }" />
            <span class="error_message">{{ birth_dateError }}</span>
          </div>
          <div class="form_wrapper my-3" v-if="allowedToSearchJuvenileByFullName">
            <label class="input_label"> Bolani markazlar bo'yicha qidirish </label>
            <button class="px-[12px] py-[6px] bg-primary h-[45px] w-[150px] text-white hover:opacity-40 rounded"
              v-if="allowedToSearchJuvenileByFullName" @click.prevent="searchJuvenileByFullName">
              Qidirish
            </button>
          </div>
          <div class="form_wrapper my-3">
            <label class="input_label"> Jinsi </label>
            <ButtonGroup :disabled="isIntegrationData" id="gender" v-model:selected-item="gender"
              :titles="btnGroupGenders" />
            <span class="error_message">{{ genderError }}</span>
          </div>

          <div v-if="allowedToFillBirthPlaceInfo" class="form_wrapper my-3">
            <label class="input_label" for="birth_region"> Tug'ilgan Viloyat / Shahar </label>
            <SelectBox id="birth_region" v-model="birth_region" :options="regionsList" show-by="name" track-by="id"
              placeholder="Viloyatni tanlang" @input="birthPlaceFetchDistrictsByRegionId" />
            <span class="error_message">{{ birth_regionError }}</span>
          </div>
          <div v-if="allowedToFillBirthPlaceInfo" class="form_wrapper my-3">
            <label class="input_label" for="birth_district"> Tug'ilgan Tumani </label>
            <SelectBox id="birth_district" v-model="birth_district" :options="birthPlaceDistrictList" show-by="name"
              track-by="id" placeholder="Tumanni tanlang" />
            <span class="error_message">{{ birth_districtError }}</span>
          </div>
        </div>
        <div class="2xl:w-[60%] flex flex-col items-start">
          <div class="form_wrapper my-3">
            <label id="photo" class="input_label"> Rasmi </label>
            <FileInput :disabled="disabledFile" :id="'photo1'" v-model:files="photo" :multiple-files="false"
              :initial-files="initialUserPhoto">
              <template #icon>
                <div class="flex justify-between shadow rounded w-full">
                  <p
                    class="flex items-center text-gray-400 px-3 overflow-hidden overflow-ellipsis base_input border-none">
                    Rasmni yuklang</p>
                  <span class="flex px-4 items-center justify-center rounded bg-primary text-white tracking-wide text-sm"
                    :class="{ 'cursor-pointer': !disabledFile }"> YUKLASH </span>
                </div>
              </template>
            </FileInput>
            <span class="error_message">{{ photoError }}</span>
          </div>
        </div>
      </section>
      <button class="px-3 m-2 py-2 border bg-primary text-white rounded-md" type="submit"
        :disabled="isSubmitting">Saqlash</button>
    </form>
  </div>
</template>

<script setup>
import { vMaska } from 'maska';
import { useField, useForm } from 'vee-validate';
import * as yup from 'yup';
import { computed, defineEmits, defineProps, nextTick, onMounted, reactive, ref, toRefs, watch } from 'vue';
import ModalComponent from '../../../Common/Modal/ModalComponent.vue';
import { minValidatorMessage, requiredValidatorMessage } from '../../../../utils/validatorMessages';
import useCountries from '../../../../composables/selectbox-list/useCountries';
import usePassportTypes from '../../../../composables/selectbox-list/usePassportTypes';
import SelectBox from '../../../Common/SelectBox';
import FileInput from '../../../Common/InputFile/FileInput';
import useRegion from '../../../../composables/selectbox-list/useRegion';
import useDistrict from '../../../../composables/selectbox-list/useDistrict';
import { btnGroupGenders } from '../../../../utils/buttonGroupTitles';
import dayjs from 'dayjs';
import useScrollToError from '../../../../composables/useScrollToError';
import ButtonGroup from '../../../Common/ButtonGroup';
import SearchService from '../../../../services/search.service';
import { GCP } from '../../../../services/gcp.service';
import PassportCard from '../../../PassportCard';
import useAlert from '../../../../composables/useAlert';
import DateComponent from '../../../Common/DateComponent';
import { Icon } from '@iconify/vue';
import { useCyrillicToLatin } from '../../../../composables/useCyrillicToLatin';
import { useLatinToCyrilic } from '../../../../composables/useLatinToCyrilic';

const { handleSubmit, isSubmitting, resetForm } = useForm();
const { countriesList } = useCountries();
const { passportTypes } = usePassportTypes();
const { regionsList } = useRegion();
const { districtsList: birthPlaceDistrictList, fetchDistrictsByRegionId: birthPlaceFetchDistrictsByRegionId } = useDistrict();
const { scrollToError } = useScrollToError();

const show_error = ref(false);
const modalList = ref();
const isIntegrationData = ref(false);
const disabledFile = ref(false);
const emit = defineEmits(['submit', 'juvenile-is-found']);
const props = defineProps({
  initialFields: {
    type: Object,
    default: function () {
      return null;
    },
  },
  clearFormMode: {
    type: Boolean,
    default: false,
  },
  isEditMode: {
    type: Boolean,
    default: false,
  },
});
const { initialFields, clearFormMode } = toRefs(props);

onMounted(() => {
  if (initialFields.value) {
    initializeFields(initialFields.value);
  }
});

function initializeFields(fieldsFromServer) {
  gender.value = fieldsFromServer.gender;
  father_name.value = fieldsFromServer.father_name;
  first_name.value = fieldsFromServer.first_name;
  last_name.value = fieldsFromServer.last_name;
  pinfl.value = fieldsFromServer.pinfl;
  passport_seria.value = fieldsFromServer.passport_seria;
  passport_type.value = fieldsFromServer.passport_type;
  birth_date.value = fieldsFromServer.birth_date;
  if (fieldsFromServer.photo) {
    initialUserPhoto.value = [fieldsFromServer.photo];
  }
  if (fieldsFromServer.reference_type) {
    initialReferenceFile.value = [fieldsFromServer.reference_type];
  }
  if (fieldsFromServer.birth_region) {
    birth_region.value = fieldsFromServer.birth_region.id;
    birth_district.value = fieldsFromServer.birth_district.id;
    birthPlaceFetchDistrictsByRegionId({ target: { value: fieldsFromServer.birth_region.id } });
  }
}

const ANOTHER_PASSPORT_TYPE = 6;
const FOREIGN_COUNTRY_PASSPORT_TYPE = 5;
const BIOMETRIK_PASSPORT_TYPE = 1;
const ID_CARD_TYPE = 2;
const BIRTH_CERTIFICATE = 3;
const KINDER_PASSPORT = 4;
const birthDateRange = reactive({
  from: dayjs().subtract(18, 'year').format('YYYY-MM-DD'),
  to: dayjs().subtract(3, 'year').format('YYYY-MM-DD'),
});

console.log(birthDateRange);

const { alertIt } = useAlert();

const initialUserPhoto = ref([]);
const initialReferenceFile = ref([]);

const selectedJuvenile = ref({
  id: '',
  personal_info: null,
  address_info: null,
  education_info: null,
  parent_info: null,
});

const notFoundError = ref('');

const juvenile_table_data = ref([]);
// ###### METHODS #######

function onInvalidSubmit({ errors }) {
  console.log(errors);
  scrollToError(Object.keys(errors)[0]);
  show_error.value = true;
}

const onSubmit = handleSubmit(async ({ birth_date, birth_district, birth_region, reference_type, foreign_country, pinfl, passport_seria, passport_number, ...args }) => {
  try {
    const res = await GCP.getIsConvicted({
      pinfl: pinfl,
      birthDate: birth_date,
      passportSerial: passport_seria,
      passportNumber: passport_number,
      firstName: useLatinToCyrilic(args.first_name),
      lastName: useLatinToCyrilic(args.last_name),
    });
    if (res.data.resultText === 'Сўров жараёнда') {
      alertIt('Bu bola sudlangan siz buni tizimga qosha olmaysiz', 'error');
    } else {
      const payloadObj = {
        is_identified: true,
        birth_date: dayjs(birth_date).format('YYYY-MM-DD'),
        ...args,
      };
      reference_type ? (payloadObj.reference_type = reference_type) : null;
      foreign_country ? (payloadObj.foreign_country = foreign_country) : null;
      pinfl ? (payloadObj.pinfl = pinfl) : null;
      passport_seria ? (payloadObj.passport_seria = `${passport_seria.toUpperCase()}${passport_number}`) : null;
      birth_district ? (payloadObj.birth_district = birth_district) : null;
      birth_region ? (payloadObj.birth_region = birth_region) : null;

      const formData = new FormData();
      Object.keys(payloadObj).forEach((k) => {
        formData.append(k, payloadObj[k]);
      });
      emit('submit', formData);
    }
  } catch (e) {
    console.log(e);
  }
}, onInvalidSubmit);

const juvenileSelectHandler = () => {
  emit('juvenile-is-found', {
    is_been_in_center: true,
    ...selectedJuvenile.value,
  });
};

const juvenileDeleteHandler = () => {
  selectedJuvenile.value = {
    personal_info: null,
    address_info: null,
    education_info: null,
    parent_info: null,
    id: '',
  };
};

const dataUrlToFile = async (dataUrl, fileName) => {
  const res = await fetch(dataUrl);
  const blob = await res.blob();
  return new File([blob], fileName, { type: 'image/png' });
};

const requestPassportData = async () => {
  try {
    const resp = await GCP.getPassportData({
      pinfl: pinfl.value,
      passport_data: `${passport_seria.value.toUpperCase()}${passport_number.value}`,
    });
    if (resp.status === 200) {
      if (new Date(birthDateRange.from).getTime() < new Date(resp.data?.data[0].birth_date).getTime()) {
        if (new Date(birthDateRange.to).getTime() > new Date(resp.data?.data[0].birth_date).getTime()) {
          const file = resp.data?.data[0].photo ? await dataUrlToFile(`data:image/png;base64,${resp.data?.data[0].photo}`, `${resp.data?.data[0].surnamelat.toLowerCase()}.png`) : '';
          photo.value = file;
          disabledFile.value = !!file;
          first_name.value = resp.data?.data[0].namelat
          father_name.value = resp.data?.data[0].patronymlat
          last_name.value = resp.data?.data[0].surnamelat
          birth_date.value = new Date(resp.data?.data[0].birth_date);
          gender.value = resp.data?.data[0].sex === 1 ? "M" : "F";
          isIntegrationData.value = true;
        } else {
          alertIt("Bola 3 yoshga to'lmagan", 'error');
        }
      } else {
        alertIt("Bu shaxs voyaga yetib bo'lgan", 'error');
      }
    }
  } catch (error) {
    alertIt(error?.response?.data?.detail, 'error');
  }
};

const requestBirthPassportData = async (data) => {
  if (data) {
    data.birth_date = data?.birth_date.split('.').reverse().join('-');
    pinfl.value = data?.pnfl;
    first_name.value = await useCyrillicToLatin(data?.name);
    father_name.value = await useCyrillicToLatin(data?.patronym);
    last_name.value = await useCyrillicToLatin(data?.surname);
    birth_date.value = new Date(data?.birth_date);
    gender.value = data?.gender_code == 1 ? 'M' : 'F';
    modalList.value.closeModal();
    isIntegrationData.value = true;
  } else {
    try {
      const resp = await GCP.getBirthPassportData({
        doc_seria: passport_seria.value.toUpperCase(),
        doc_number: passport_number.value,
      });
      if (resp.status === 200) {
        juvenile_table_data.value = resp.data.items;
        if (resp.data?.items.length && resp.data?.items.length === 1) {
          const data = resp.data?.items[0];
          data.birth_date = data?.birth_date.split('.').reverse().join('-');
          if (new Date(birthDateRange.from).getTime() < new Date(data?.birth_date).getTime()) {
            if (new Date(birthDateRange.to).getTime() > new Date(data?.birth_date).getTime()) {
              pinfl.value = data?.pnfl;
              first_name.value = await useCyrillicToLatin(data?.name);
              father_name.value = await useCyrillicToLatin(data?.patronym);
              last_name.value = await useCyrillicToLatin(data?.surname);
              birth_date.value = new Date(data?.birth_date);
              gender.value = data?.gender_code == 1 ? 'M' : 'F';
              isIntegrationData.value = true;
            } else {
              alertIt("Bola 3 yoshga to'lmagan", 'error');
            }
          } else {
            alertIt("Bu shaxs voyaga yetib bo'lgan", 'error');
          }
        } else if (resp.data?.items.length > 1) {
          modalList.value.openModal();
        } else {
          alertIt('Bola topilmadi', 'error');
        }
      }
    } catch (error) {
      alertIt(error?.response?.data?.detail, 'error');
    }
  }
};

const searchJuvenileInCenters = async (params) => {
  const { data: result } = await SearchService.searchJuvenileInCenters(params);
  selectedJuvenile.value = result;
  alertIt("Bola markazda avval bo'lgan!", 'success');
  await nextTick();
  scrollToError(result.id);
  notFoundError.value = '';
};

const searchJuvenile = async (params) => {
  try {
    await searchJuvenileInCenters(params);
  } catch (error) {
    try {
      if (allowedToGCP.value) {
        await requestPassportData();
        await searchJuvenileInCenters(params);
      } else if (allowBirthCertificate.value) {
        await requestBirthPassportData();
        await searchJuvenileInCenters(params);
      }
    } catch (error) {
      alertIt(error?.response?.data?.detail || "Ma'lumot kelishida hatolik mavjud", 'error');
    }
  }
};

const searchJuvenileByPINFL = () => {
  searchJuvenile({
    juvenile_pinfl: pinfl.value,
  });
};

const searchJuvenileByFullName = () => {
  searchJuvenile({
    first_name: first_name.value,
    last_name: last_name.value,
    birth_date: dayjs(birth_date.value).format('YYYY-MM-DD'),
  });
};

// ###### METHODS #######

// COMPUTEDS
const allowBirthCertificate = computed(() => {
  return passport_type.value === BIRTH_CERTIFICATE;
});

const allowedToPutForeignCountry = computed(() => {
  return passport_type.value === FOREIGN_COUNTRY_PASSPORT_TYPE;
});

const dynamicValidationForForeignCountry = computed(() => {
  return passport_type.value === FOREIGN_COUNTRY_PASSPORT_TYPE ? yup.number().nullable().required(requiredValidatorMessage) : yup.number().nullable().notRequired();
});

const allowedToGCP = computed(() => {
  return passport_type.value === BIOMETRIK_PASSPORT_TYPE || passport_type.value === ID_CARD_TYPE || passport_type.value === KINDER_PASSPORT;
});

const allowedToFillSeriyaNumber = computed(() => {
  return passport_type.value !== ANOTHER_PASSPORT_TYPE && passport_type.value !== FOREIGN_COUNTRY_PASSPORT_TYPE;
});

const allowedToFillSeriyaNumberForPINFL = computed(() => {
  return passport_type.value !== ANOTHER_PASSPORT_TYPE && passport_type.value !== FOREIGN_COUNTRY_PASSPORT_TYPE && passport_type.value !== BIRTH_CERTIFICATE;
});

const allowedToPutReferenceType = computed(() => {
  return passport_type.value === FOREIGN_COUNTRY_PASSPORT_TYPE || passport_type.value === ANOTHER_PASSPORT_TYPE;
});

const allowedToFillBirthPlaceInfo = computed(() => {
  return passport_type.value !== FOREIGN_COUNTRY_PASSPORT_TYPE;
});

// const allowedToSearchJuvenileByPINFL = computed(() => {
//   return allowedToFillSeriyaNumber.value && pinfl.value.length === 14;
// });

const allowedToSearchJuvenileByFullName = computed(() => {
  return first_name.value && last_name.value && birth_date.value && passport_type.value === FOREIGN_COUNTRY_PASSPORT_TYPE;
});

const dynamicValidatorForPassportSeria = computed(() => {
  if ((passport_type.value && passport_type.value === BIOMETRIK_PASSPORT_TYPE) || passport_type.value === ID_CARD_TYPE || passport_type.value === BIRTH_CERTIFICATE) {
    return yup.string().required(requiredValidatorMessage);
  } else return yup.string().notRequired();
});

const dynamicValidatorForReferenceTypeFile = computed(() => {
  if (passport_type.value === ANOTHER_PASSPORT_TYPE) {
    return yup.mixed().nullable().required(requiredValidatorMessage);
  } else {
    return yup.mixed().nullable().notRequired();
  }
});

const dynamicValidatorForBirthPlaceInfo = computed(() => {
  if (allowedToFillBirthPlaceInfo.value) {
    return yup.string().required(requiredValidatorMessage);
  } else {
    return yup.string().notRequired();
  }
});

const dynamicValidatorForPinfl = computed(() => {
  if ((passport_type.value && passport_type.value === BIOMETRIK_PASSPORT_TYPE) || passport_type.value === ID_CARD_TYPE) {
    return yup.string().required(requiredValidatorMessage).min(14, minValidatorMessage);
  } else if (passport_type.value && passport_type.value === BIRTH_CERTIFICATE && pinfl.value.length > 0) {
    return yup.string().notRequired().min(14, minValidatorMessage);
  } else {
    return yup.string().notRequired();
  }
});

// COMPUTEDS

// FIELDS

const { value: passport_type, errorMessage: passport_typeError } = useField('passport_type', yup.number().required(requiredValidatorMessage));

const { value: foreign_country, errorMessage: foreign_countryError } = useField('foreign_country', dynamicValidationForForeignCountry, {
  initialValue: null,
});

