<template>
  <Datepicker
    :modelValue="modelValue"
    @update:modelValue="handleDate"
    :placeholder="placeholder"
    locale="uzb"
    input-class-name="base_input"
    :format="computedOptions.format"
    :auto-apply="computedOptions.autoApply"
    :flow="computedOptions.flow"
    :year-range="computedOptions.yearRange"
    :range="computedOptions.range"
    :min-date="computedOptions.minDate"
    :max-date="computedOptions.maxDate"
    :start-date="computedOptions.startDate"
    :multi-calendars="computedOptions.multiCalendars"
  />
</template>

<script setup>
import Datepicker from '@vuepic/vue-datepicker';
import {computed, defineEmits, defineProps, toRefs, watch} from 'vue';
import '@vuepic/vue-datepicker/dist/main.css';

const emit = defineEmits(['update:modelValue']);
// const vModel = ref(null);
watch(
  () => props.modelValue,
  (val) => {
    console.log(val);
  }
);

// watch(
//   () => vModel.value,
//   () => {
//     emit('update:modelValue', vModel.value);
//   }
// );
const defaultOptions = {
  format: 'dd.MM.yyyy',
  range: false,
  multiCalendars: false,
  autoApply: true,
  flow: [],
  minDate: null,
  maxDate: null,
  startDate: null,
  yearRange: [1800, 2400],
};
const computedOptions = computed(() => ({...defaultOptions, ...options.value}));

const props = defineProps({
  modelValue: {
    required: true,
  },
  options: {
    type: Object,
  },
  placeholder: {
    type: String,
    default: '',
  },
});
const {options, placeholder} = toRefs(props);

const handleDate = (value) => {
  emit('update:modelValue', value);
};
</script>

<style scoped></style>
